.portfolio-main {
  margin-top: 1em;
  width: 100%;
}

.portfolio-main > div {
  flex: 100%;
}

.portfolio-main > div > a {
  color: var(--main-text-color);
  text-decoration: none;
}

.portfolio-main > div > a:hover {
  color: var(--main-text-hover-color);
}

.portfolio-main > div > a > img {
  height: 6.5em;
}

.portfolio-main > div > a > h2 {
  margin-top: 0.5em;
}

.past-projects > div {
  flex: 50%;
  padding-right: 0.75em;
}

.past-projects > div > h2:first-child {
  margin-top: 0.5em;
}

.past-projects > div > img {
  float: right;
}

