.contact {
  margin-top: 3em;
  width: 100%;
}

.contact > div {
  flex: 33.33333%;
}

.contact > div > a {
  color: var(--main-text-color);
  text-decoration: none;
}

.contact > div > a:hover {
  color: var(--main-text-hover-color);
}

.contact > div > a > img {
  height: 6.5em;
}
.contact > div > a > div {
  font-family: MyUniverse;
  font-size: 1.8em;
  padding-top: 0.5em;
}
